import {
  Text,
  Link,
  Spacing,
} from '@vkontakte/vkui';
import State from '../../store/State';
import { modals, ratings } from '../../types/enums';
import { observer } from 'mobx-react-lite';
import '../../css/rating.css';


export const RatingInfoBlock = observer(() => {
  return (
    <div className='rating-info-block'>
      <Text weight='2'>
        {State.getRatingCategory() === ratings.TOP_ALL
          ?
          '         .   ,      ,    .'
          :
          <>
                   .      
            <Link href={'https://vk.com/public' + process.env.REACT_APP_GROUP} target="_blank">  </Link>
            .
          </>
        }
      </Text>
      <Spacing size={5} />
      <Link onClick={() => State.setActiveModal(modals.RULES)} >  </Link>
    </div>
  );
})

