import { Panel, Button } from '@vkontakte/vkui';
import '../css/intro.css';
import State from '../store/State';
import { routes } from '../types/enums';

export const IntroFirst = ({ id }: IpanelProps) => {
  return (
    <Panel id={id}>
      <div className='intro'>
        <div className='intro-header'> <br /> <b> !</b></div>
        <div className='intro-logo' />
        <div className='intro-descr'>     <br />  .</div>
        <div className='intro-button'>
          <Button size='m' onClick={() => {
            State.goToPage(routes.INTROSECOND)
          }}
          >
            
          </Button>
        </div>
        <div className='intro-continue-info'>
            ,    <span className='intro-continue-info-link' onClick={() => { State.goToPage(routes.RULES) }}>  </span>
        </div>
      </div>
    </Panel>
  );
}